@import url("https://fonts.googleapis.com/css?family=Work+Sans:400,600&subset=latin-ext");
.header {
  text-align: center; }
  .header__lede {
    font-size: 2.4em;
    margin-bottom: 0; }
  .header__subhed {
    color: #777;
    font-size: 1.2em; }

.error-message {
  border: 3px solid #E82C0C;
  color: #777;
  max-width: 600px;
  margin: 20px auto;
  padding: 20px; }
  .error-message__hed {
    color: #E82C0C;
    font-size: 1.6em;
    margin: 0;
    text-align: center; }
  .error-message__list {
    color: #E82C0C; }
    .error-message__list-item {
      margin: .5em 0; }
  .error-message__foot {
    color: #777;
    font-size: 0.8em;
    text-align: center; }

#root {
  color: #222;
  font-family: 'Work Sans', sans-serif;
  letter-spacing: -0.2px;
  padding: 50px 20px; }



